/* Angular-UI-Bootstrap FIX */
.nav, .pagination, .carousel, .panel-title a { cursor: pointer; }

.timeEntry .time-actions {
  opacity: 0.3;
  -webkit-transition: opacity ease .2s;
  -moz-transition: opacity ease .2s;
  -o-transition: opacity ease .2s;
  transition: opacity ease .2s;
}

.timeEntry:hover .time-actions {
  opacity: 1;
}

.timeEntry .time-actions ul {
  margin-bottom: 0;
}

.timeEntry .time-actions ul li {
  padding-right: 3px;
  padding-left: 3px;
}

.timeEntry .time-actions a {
  color: #333;
}